id=\"+this.cityid+\"&is_new_local=true\"\r\n            var that=this;\r\n            Ajax.vueJson(url1,function(data){\r\n                console.log(data);\r\n                that.list=data;\r\n               that.dis=true;\r\n            },function(err){console.log(err)})\r\n\r\n\r\n        },\r\n        watch:{\r\n            cityid(new1,old){\r\n                // console.log(new1,old)\r\n                 var url1=\"https://api.ricebook.com/hub/home/v1/virtual/category.json?city_id=\"+new1+\"&is_new_local=true\"\r\n                    var that=this;\r\n                    Ajax.vueJson(url1,function(data){\r\n                        // console.log(data);\r\n                        that.list=data;\r\n                         that.dis=true;\r\n                    },function(err){console.log(err)})\r\n            }\r\n        },\r\n        methods:{\r\n            kinds(id){\r\n                console.log(id);\r\n                this.$router.push({\r\n                    path:\"kinds\",\r\n                    query:{\r\n                        id:id\r\n                    }\r\n                })\r\n            },\r\n            show(){\r\n                if(this.ishow){\r\n                    this.ishow=false;\r\n                }else{\r\n                    this.ishow=true;\r\n                }\r\n\r\n            },\r\n             show11(){\r\n                if(this.show1){\r\n                    this.show1=false;\r\n                }else{\r\n                    this.show1=true;\r\n                }\r\n\r\n            },\r\n            xuan(event){\r\n                // console.log(event.target.innerText)\r\n                var arr=this.city;\r\n                var cheng=event.target.innerText;\r\n                this.chengshi=cheng;\r\n                // console.log(event.target.getAttribute(\"cityid\"))\r\n                  this.cityid=event.target.getAttribute(\"cityid\");\r\n                localStorage.setItem(\"city\",event.target.getAttribute(\"cityid\"))\r\n\r\n               \r\n                    this.ishow=false;\r\n                    console.log(this.cityid,\"结束\")\r\n              \r\n                    \r\n            }\r\n        }\r\n    }\r\n</script>\r\n\r\n<style scoped>\r\n#content .jiazai{\r\n            position: fixed;\r\n            top:50%;\r\n            left: 50%;\r\n            transform: translate(-30px,-30px);\r\n            \r\n    }\r\n   \r\n</style>\r\n"],"sourceRoot":""}]);

