show1\">\r\n                <div class=\"sou1\">\r\n                    <input type=\"text\" placeholder=\"搜索本地精选 / 快递到家\" />\r\n                    <span @click=\"search()\">搜索</span>\r\n                    <div class=\"jiao\"></div>\r\n                </div>\r\n            </div>\r\n        </header>\r\n        <img v-if=\"!dis\" class=\"jiazai\" src=\"tool/img.gif\"/>\r\n        <div id=\"content\" v-if=\"dis\">\r\n            <div class=\"list\">\r\n                <div class=\"list1\"  v-for=\"it in list\">\r\n                    <h3>{{it.group_section.title}} </h3>\r\n                    <p>{{it.group_section.desc}}</p>\r\n                    <ul>\r\n                        <li v-for=\"i in it.tabs\" @click=\"delate(i.enjoy_url)\">\r\n                            <img class=\"lazy\" src=\"tool/img.gif\" :data-echo=\"i.url\"/>     \r\n                              <!-- <img :src=\"i.url\"/>   -->\r\n                            <div class=\"title\">{{i.title}}</div>\r\n                            <div class=\"title2\">{{i.desc}} </div>\r\n                        </li>\r\n                    </ul>\r\n                </div>\r\n            </div>\r\n            <div class=\"city\" v-show=\"ishow\">\r\n                <h1>本地服务开通城市</h1>\r\n                <div class=\"citys\">\r\n                    <span v-for=\"(i,index) in city\">\r\n                           <div v-for=\"(it,ind) in i\"  @click=\"xuan($event)\" :cityid=\"ind\">{{it}}</div>\r\n                    </span>\r\n                    \r\n                </div>\r\n            </div>\r\n        </div>\r\n    </div>\r\n</template>\r\n\r\n\r\n<script>\r\nimport Vue from \"vue\";\r\nimport VueRouter from \"vue-router\";\r\nimport { Toast } from 'mint-ui';\r\nimport \"./../scss/main1.scss\";\r\nimport Ajax from \"./../tool/MyAjax\";\r\n\r\n\r\nVue.use(VueRouter)\r\n    var router=new VueRouter({\r\n   \r\n        }) \r\n    export default {\r\n        \r\n        data(){\r\n            return {\r\n               ishow:false,\r\n               deng:true,\r\n               show1:false,\r\n               cityid:104,\r\n               list:[],\r\n                num:0,\r\n                city:[{104:\"上海\"},{140:\"北京\"},{144:\"南京\"},{185:\"天津\"},{216:\"广东\"},{235:\"成都\"},\r\n               {260:\"杭州\"},{299:\"深圳\"},{347:\"苏州\"},{362:\"西安\"},{388:\"重庆\"},{401:\"长沙\"}],\r\n               chengshi:\"\",\r\n               dis:false,\r\n               tian:false\r\n            }\r\n        },\r\n        watch:{\r\n            cityid(new1,old){\r\n                // console.log(new1,old)\r\n                this.list=[];\r\n                 var url1=\"https://api.ricebook.com/hub/home/v1/web/week_choice.json?city_id=\"+this.cityid+\"&page=\"+this.num;\r\n                   \r\n                   var that=this;\r\n                    Ajax.vueJson(url1,function(data){\r\n                        // console.log(data);\r\n                        that.list=data;\r\n                    \r\n                    },function(err){console.log(err)})\r\n            }\r\n        },\r\n        methods:{\r\n            search(){\r\n                var word=$(\".sou1 input\").val()\r\n                if(word!=\"\"){\r\n                     router.push({\r\n                        path:\"search\",\r\n                        query:{\r\n                            query_k:word\r\n                        }\r\n                    })\r\n                }\r\n                   \r\n            },\r\n            tui(){\r\n                this.deng=true;\r\n                localStorage.removeItem(\"user\")\r\n            },\r\n            xiao(){\r\n                console.log(\"已经登录过了\")\r\n              if(!this.tian){\r\n                    this.tian=true;\r\n                    $(\".tuichu1\").animate({\r\n                        opacity:\"1\"\r\n                    })\r\n              }else{\r\n                   this.tian=false;\r\n                    $(\".tuichu1\").animate({\r\n                        opacity:\"0\"\r\n                    })\r\n              }\r\n               \r\n            },\r\n            delate(data){\r\n                \r\n                var arr=data.split(\"?\")[1]\r\n                console.log(arr);\r\n                router.push({\r\n                    path:\"detail\",\r\n                    query:{\r\n                        url:arr\r\n                    }\r\n                })\r\n                \r\n            },\r\n            show(){\r\n                if(this.ishow){\r\n                    this.ishow=false;\r\n                }else{\r\n                    this.ishow=true;\r\n                }\r\n\r\n            },\r\n             show11(){\r\n                if(this.show1){\r\n                    this.show1=false;\r\n                }else{\r\n                    this.show1=true;\r\n                }\r\n\r\n            },\r\n             xuan(event){\r\n                // console.log(event.target.innerText)\r\n                var arr=this.city;\r\n                var cheng=event.target.innerText;\r\n                this.chengshi=cheng;\r\n                // console.log(event.target.getAttribute(\"cityid\"))\r\n                  this.cityid=event.target.getAttribute(\"cityid\");\r\n                localStorage.setItem(\"city\",event.target.getAttribute(\"cityid\"))\r\n\r\n               \r\n                    this.ishow=false;\r\n                    console.log(this.cityid,\"结束\")\r\n              \r\n                    \r\n            },\r\n            top(event){\r\n                // console.log(event.target.scrollTop)\r\n                // console.log(event.target.offsetHeight);\r\n                // console.log(event.target.scrollHeight);\r\n                 echo.init({\r\n                        offset: 0,\r\n                    　　 throttle: 0 ,\r\n                        unload: false,\r\n                        callback: function (element, op) {\r\n                        // console.log(element, 'has been', op + 'ed')\r\n                    }\r\n                    }); \r\n                var sh=event.target.scrollHeight;\r\n                var h=event.target.offsetHeight;\r\n                var t=event.target.scrollTop;\r\n                var cityid=this.cityid;\r\n                var that=this;\r\n                if(sh==h+t ){\r\n                    console.log(\"加载\");\r\n                    this.num++;\r\n                    // console.log(this.num);\r\n                     var url = \"https://api.ricebook.com/hub/home/v1/web/week_choice.json?city_id=\"+cityid+\"&page=\"+this.num;\r\n                        Ajax.vueJson(url,function(data){\r\n                            // console.log(data);\r\n                            for(var itm of data){\r\n                                that.list.push(itm)\r\n                            }\r\n                            \r\n                        },function(err){console.log(err)})\r\n                }\r\n            }\r\n\r\n            \r\n            \r\n        },\r\n        computed:{\r\n           \r\n        },\r\n        mounted(){\r\n                if(localStorage.getItem(\"user\")){\r\n                    this.deng=false;\r\n                }else{\r\n                    this.deng=true;\r\n                }\r\n                if(localStorage.getItem(\"city\")){\r\n                    this.cityid=localStorage.getItem(\"city\")\r\n                    var arr=this.city;\r\n                    for(var it of arr){\r\n                        // console.log(it,\"aaaa\")\r\n                        for(var i in it){\r\n                            \r\n                            if(i==localStorage.getItem(\"city\")){\r\n                                console.log(it[i])\r\n                                this.chengshi=it[i]\r\n                            }\r\n                        }\r\n                    }\r\n                }else{\r\n                    localStorage.setItem(\"city\",this.cityid);\r\n                    var arr=this.city;\r\n                    for(var it of arr){\r\n                        console.log(it,\"aaaa\")\r\n                        for(var i in it){\r\n                            if(i==localStorage.getItem(\"city\")){\r\n                                 this.chengshi=it[i]\r\n                            }\r\n                        }\r\n                    }\r\n                }\r\n              var cityid=this.cityid;\r\n                var that =this\r\n                var url = \"https://api.ricebook.com/hub/home/v1/web/week_choice.json?city_id=\"+cityid+\"&page=0\";\r\n                Ajax.vueJson(url,function(data){\r\n                    console.log(data);\r\n                    that.list=data\r\n                    that.dis=true;\r\n                },function(err){console.log(err)})\r\n            \r\n        },\r\n        updated(){\r\n            echo.init({\r\n                offset: 0,//离可视区域多少像素的图片可以被加载\r\n            　　 throttle: 0 ,//图片延时多少毫秒加载\r\n                unload: false,\r\n                callback: function (element, op) {\r\n\t\t         console.log(element, 'has been', op + 'ed')\r\n\t\t    }\r\n            }); \r\n        }\r\n            \r\n\r\n    \r\n    }\r\n   \r\n</script>\r\n\r\n<style scoped>\r\n    #content{\r\n        display: block;\r\n        background: #fff;\r\n    }\r\n   \r\n    /* .list1 img{ \r\n　　　　width:90%; \r\n　　　　height: 200px; \r\n　　　　background: url(\"tool/img.gif\") 50% no-repeat;\r\n    }  */\r\n     .jiazai{\r\n            position: fixed;\r\n            top:50%;\r\n            left: 50%;\r\n            transform: translate(-30px,-30px);\r\n            \r\n    }\r\n    .abc{\r\n          position: relative;  \r\n    }\r\n    .tuichu1{\r\n        text-align: center;\r\n        position: absolute;\r\n        top:24px;\r\n        color: #000;\r\n        text-shadow: 0 0 0 #000;\r\n        width: 35px;\r\n        opacity: 0;\r\n        \r\n    }\r\n\r\n    \r\n</style>\r\n"],"sourceRoot":""}]);

