y_url)\">{{i.data.group_section.enjoy_url_text}}</div>\r\n                        </div>\r\n                        <div class=\"imgs\">\r\n                            <img v-for=\"(its,ind) in i.data.tabs\" :key=\"ind\" :src=\"its.url\"  @click=\"delate(its.enjoy_url)\"  />\r\n                        </div>\r\n                   \r\n                </div>\r\n            </div> \r\n        </div>\r\n        \r\n    </div>\r\n</template>\r\n\r\n\r\n<script>\r\nimport Vue from \"vue\";\r\nimport VueRouter from \"vue-router\";\r\nimport { Toast } from 'mint-ui';\r\nimport \"./../scss/main.scss\";\r\nimport \"./../scss/dis.scss\";\r\nimport Ajax from \"./../tool/MyAjax\";\r\nVue.use(VueRouter)\r\n    var router=new VueRouter({\r\n   \r\n        }) \r\n    export default {\r\n        \r\n        data(){\r\n            return {\r\n               show1:false,\r\n               cityid:104,\r\n               list:[],\r\n                num:0,\r\n                city:[{104:\"上海\"},{140:\"北京\"},{144:\"南京\"},{185:\"天津\"},{216:\"广东\"},{235:\"成都\"},\r\n               {260:\"杭州\"},{299:\"深圳\"},{347:\"苏州\"},{362:\"西安\"},{388:\"重庆\"},{401:\"长沙\"}],\r\n               chengshi:\"\",\r\n               xuan1:[],\r\n               dis:false\r\n              \r\n            }\r\n        },\r\n        methods:{\r\n            search(){\r\n                var word=$(\".sou1 input\").val()\r\n                if(word!=\"\"){\r\n                     router.push({\r\n                        path:\"search\",\r\n                        query:{\r\n                            query_k:word\r\n                        }\r\n                    })\r\n                }\r\n                   \r\n            },\r\n            delate(data){\r\n                \r\n                var arr=data.split(\"?\")[1]\r\n                console.log(arr);\r\n                router.push({\r\n                    path:\"detail\",\r\n                    query:{\r\n                        url:arr\r\n                    }\r\n                })\r\n                \r\n            },\r\n            dislist(data){\r\n                console.log(data)\r\n                var arr=data.split(\"?\")[1]\r\n                console.log(arr);\r\n                router.push({\r\n                    path:\"dislist\",\r\n                    query:{\r\n                        url:arr\r\n                    }\r\n                })\r\n            },\r\n           \r\n            show11(){\r\n                if(this.show1){\r\n                    this.show1=false;\r\n                }else{\r\n                    this.show1=true;\r\n                }\r\n\r\n            }\r\n        \r\n           \r\n        },\r\n        updated(){\r\n            console.log(111)\r\n            var swiper = new Swiper('#disbanner1', {\r\n                pagination: '#dispint',\r\n                slidesPerView: 'auto',\r\n                centeredSlides: true,\r\n                paginationClickable: true,        \r\n                paginationType : 'fraction'\r\n            })\r\n             var swiper = new Swiper('#riben1', {       \r\n                slidesPerView: 'auto',\r\n                paginationClickable: true,\r\n                slidesPerView: 2,\r\n               spaceBetween: 20\r\n               \r\n            })\r\n        },\r\n        mounted(){\r\n           \r\n             \r\n                if(localStorage.getItem(\"city\")){\r\n                    this.cityid=localStorage.getItem(\"city\")\r\n                    var arr=this.city;\r\n                    for(var it of arr){\r\n                        // console.log(it,\"aaaa\")\r\n                        for(var i in it){\r\n                            \r\n                            if(i==localStorage.getItem(\"city\")){\r\n                                console.log(it[i])\r\n                                this.chengshi=it[i]\r\n                            }\r\n                        }\r\n                    }\r\n                }else{\r\n       